import { NextResponse } from "next/server";
import { createClient } from "@supabase/supabase-js";

const supabase = createClient(
  process.env.NEXT_PUBLIC_SUPABASE_URL!,
  process.env.NEXT_PUBLIC_SUPABASE_ANON_KEY! // anon key is fine for read-only
);

export async function GET() {
  try {
    // 1. Fetch categories
    const { data: categories, error: categoriesError } = await supabase
      .from("insurance_categories")
      .select("*");

    if (categoriesError) {
      throw categoriesError;
    }

    // 2. Fetch companies (with category info)
    const { data: companies, error: companiesError } = await supabase
      .from("insurance_companies")
      .select("id, name, logo_url, category_id");

    if (companiesError) {
      throw companiesError;
    }

    // 3. Group companies under categories
    const grouped: Record<string, any> = {};
    categories.forEach((cat) => {
      grouped[cat.name] = {
        category: {
          id: cat.id,
          name: cat.name,
          icon: cat.icon || "ðŸ¢",
          description: "Category description placeholder", // add real desc later if you have column
        },
        companies: companies.filter((c) => c.category_id === cat.id),
      };
    });

    return NextResponse.json({ companies: grouped });
  } catch (error: any) {
    console.error(error);
    return NextResponse.json(
      { error: "Failed to fetch companies" },
      { status: 500 }
    );
  }
}
